// Base class for all models.
var BaseModel = require('models/model');
var app = require('application');
module.exports = BaseModel.extend({

  // urlRoot: function(){
  //   return app.API + 'users';
  // },

  // Validation 
  validation: {
    firstName: {
      required: true
    },
    lastName: {
      required: true
    },
    email: {
      required:true,
      pattern: 'email'
    },
    confirmEmail: {
      required:true,
      pattern: 'email'
    },
    password: {
      minLength: 8
    },
    repeatPassword: {
      equalTo: 'password',
      msg: 'The passwords do not match'
    },
    zipCode: {
      required:true,
      // US zip code regex
      pattern: /^\d{5}(?:[-\s]\d{4})?$/
    },
    birthday: {
      required:true,
      
    },
    termsOfUse: {
      acceptance: true
    }
  }
});
